/* Body */
.MapAdmin{
    color: #1b586d;
    width: 720px;
    background-color: #e2e9f2;
    padding-right: 25px;
    padding-left: 25px;

}

/* BalanzaDescription */
.mapAdminDescription p{
    color: #1b586d;
    text-align : justify;
    padding-top: 20px;
}

/* BalanzaInstructions */
.mapAdminInstructions p{
    text-align : left;
    padding-top: 0px;
    padding-bottom: 2px;
    font-weight:bold;
}
.mapAdminInstructions textarea{
    padding-top: 2px;
    min-width: 720px;
    min-height: 75px;
    max-width: 720px;
    max-height: 75px;
    background: none;
}
/* BalanzaValues */
.mapAdminValues{
    font-weight:bold;
    text-align: left
}
.mapAdminValues input{
    width: 200px;
}
.divPrincipalIdea{
    border-color: #1b586d;
    width: 50%;
}

.pruebainput{
    background: none;
    border-style: groove;
    border-color: #1b586d;
    border-width: 1px;
    height: 25px;
    width: 50%;
    box-sizing: border-box;
    padding-bottom: 5px;
}


.textMapAdminPrincipal{
    display: -webkit-flex;
    display: flex;
    padding-top: 5px;
}
.divPrincipalIdea input{
    background: none;
    border-style: groove;
    border-color: #1b586d;
    border-width: 1px;
    height: 25px;
    width: 100%;
    box-sizing: border-box;
    padding-bottom: 5px;
}

/*  ButtonTerminar */
.alignButtonCSS{
    padding-bottom: 10px;
    text-align: right;
}

.buttonAdminMapTerminar{
    background-color: #f06522;
    border: none;
    height: 25px;
    color: white;
    padding-left: 6px;
    padding-right: 6px;
    display: inline-block;
}
.buttonAdminMapAlineacionImagen{
	display:table-cell;
    vertical-align:middle;
    width: 15px;
    height: 15px;
	padding-right: 10px;	
}

.buttonAdminMapAlineacionImagen img{
    padding-top: 3px;
}

.buttonAdminMapAlineacionTexto{
    display:table-cell;
    vertical-align:middle;
}

/********* Mapa Student ************/

.mapStudent{
    background-image: url(/eldventir_pages/static/media/Fondo.7da25cd5.jpg);
    background-repeat: no-repeat;
    background-size: 100% 93%;
    background-position: center bottom;
    padding: 10px;
    width: 900px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.mapStudentBand{
    background-image: url(/eldventir_pages/static/media/Personaje_balanza.a83da698.png),url(/eldventir_pages/static/media/fondo-instrucciones.401c7200.png);
    background-repeat: no-repeat;
    background-position: 0px 0px, 50px 20px;
    background-size: 182px 178px, 820px 176px;
    width: 890px;
    height: 178px;

}

.mapStudentText{
    font-size: 18pt;
    text-align: left;
    margin-left: 25%;
    padding-top: 15px;
    width: 650px;
}

.mapStudentTextIns{
    padding-top: 20px;
}

.mapStudentInstructions{
    width: 90%;
}

.mapStudentContainer{
    height: 500px;
    position: relative;
}

.mapLeftSide{
    position: absolute;
    left: 0;
    top: 0; 
    width: 75%;
    height: 100%;
    display: inline-block;
}

.mapLeftSideFinished{
    position: absolute;
    left: 15%;
    top: 0; 
    width: 75%;
    height: 100%;
    display: inline-block;
}

.mapRightSide{
    position: absolute;
    right: 0;
    top: 0;    
    width: 25%;
    height: 100%;
    display: inline-block;
}

.mapStudentConceptsDisplay{
    display: inline;
    position: absolute;
    top: 30%;
}

.mapStudentConceptsDisplayT{
    display: inline;
    position: absolute;
    top: 40%;
}

.buttonStudentTerminar{
    background-color: #f06522;
    border: none;
    height: 25px;
    color: white;
    padding-left: 6px;
    padding-right: 6px;
    display: inline-block;
}

