/******************* Idea Tercer Nivel ********************************/
.containerThirdIdeaHor{
    height: 48px;
    min-width: 45%;
    max-width: 45%;
    
}

.containerThirdIdeaVer{
    width: 50px;
    min-height: 50%;
    max-width: 50%;
}


.containerThirdIdeaRig{
    width: 50px;
    min-height: 50%;
    max-width: 50%;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}

.containerThirdIdeaRig > div{
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);

}

.containerThirdIdeaLef{
    width: 50px;
    min-height: 50%;
    max-width: 50%;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}

.containerThirdIdeaLef > div{
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}


.thirdIdea{
    background-image: url(/eldventir_pages/static/media/tercera_idea.e3ff0181.svg);
